.card {
    background-color: #333;
    color: white;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
    transition: height 0.3s ease;
  }
  
  .card.expanded {
    height: auto;
  }
  
  .card-header {
    margin-bottom: 10px;
    font-family: 'Lato', sans-serif;
  }
  
  .card-body {
    overflow: hidden;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    letter-spacing: 0.5px;
    word-spacing: 2px;
  }
  
  .read-more-button {
    background-color: #555;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .read-more-button:hover {
    background-color: #777;
    
}
  